// src/components/SidebarRight.tsx
"use client";

import { useState, useEffect, useMemo } from "react";
import { Line } from "react-chartjs-2";
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Tooltip, Legend } from "chart.js";
import { useData } from "@/context/DataContext";

// Register ChartJS components
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Tooltip, Legend);

// === Tipe Data ===
interface Transect {
  id: string;
  name: string;
  length: number;
  avgDepth: number;
}

interface SurveyGroup {
  surveyId: string;
  date: string;
  transects: Transect[];
}

interface SamplingPoint {
  surveyId: string;
  distance: number;
  depth: number;
}

// === Helper: Auth Headers ===
const getAuthHeaders = () => {
  const token = localStorage.getItem("authToken");
  return {
    "Content-Type": "application/json",
    ...(token ? { Authorization: `Bearer ${token}` } : {}),
  };
};

// === Main Component ===
export default function SidebarRight() {
  const [surveyGroups, setSurveyGroups] = useState<SurveyGroup[]>([]);
  const [selectedSurveyIds, setSelectedSurveyIds] = useState<string[]>([]);
  const [allData, setAllData] = useState<Record<string, SamplingPoint[]>>({});
  const [loading, setLoading] = useState(true);
  const [loadingPoints, setLoadingPoints] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  // --- Ambil echosounderData dari context (source of truth) ---
  const { echosounderData } = useData();

  const itemsPerPage = 5;
  const totalPages = Math.ceil(surveyGroups.length / itemsPerPage);

  // --- 1. Ambil & Group Daftar Transek per surveyId ---
  useEffect(() => {
    const fetchTransects = async () => {
      setLoading(true);
      try {
        const res = await fetch("http://localhost:5000/graphql", {
          method: "POST",
          headers: getAuthHeaders(),
          body: JSON.stringify({
            query: `
              query GetTransects {
                spatialFeatures(layerType: "valid_transect_line") {
                  id
                  name
                  meta
                }
              }
            `,
          }),
        });

        const data = await res.json();

        if (data.errors) throw new Error(data.errors[0].message);

        const features = data.data?.spatialFeatures || [];

        // Group by surveyId
        const grouped: Record<string, SurveyGroup> = {};

        features.forEach((item: any) => {
          const meta = item.meta || {};
          const surveyId = meta.survey_id;

          if (!surveyId) return;

          if (!grouped[surveyId]) {
            grouped[surveyId] = {
              surveyId,
              date: meta.surveyDate || "N/A",
              transects: [],
            };
          }

          grouped[surveyId].transects.push({
            id: item.id,
            name: item.name || `Transek ${item.id}`,
            length: Math.round(meta.length || 0),
            avgDepth: parseFloat((meta.avgDepth || 0).toFixed(2)),
          });
        });

        const surveys = Object.values(grouped);
        setSurveyGroups(surveys);

        // Auto-select 1-2 survey terbaru
        if (surveys.length > 0) {
          const latest = surveys.slice(0, 2).map((s) => s.surveyId);
          setSelectedSurveyIds(latest);
        }
      } catch (err) {
        console.error("‚ùå Gagal muat daftar transek:", err);
        alert("Gagal memuat data transek. Cek koneksi atau login kembali.");

        // Fallback data
        const fallback: SurveyGroup[] = [
          {
            surveyId: "SURVEY_1756104956",
            date: "2025-04-01",
            transects: [{ id: "TR_1", name: "Transek 01", length: 1200, avgDepth: 3.4 }],
          },
          {
            surveyId: "SURVEY_1756104957",
            date: "2025-04-05",
            transects: [{ id: "TR_2", name: "Transek 02", length: 1100, avgDepth: 3.1 }],
          },
          {
            surveyId: "SURVEY_1756104958",
            date: "2025-04-06",
            transects: [{ id: "TR_3", name: "Transek 03", length: 1000, avgDepth: 3.2 }],
          },
          {
            surveyId: "SURVEY_1756104959",
            date: "2025-04-07",
            transects: [{ id: "TR_4", name: "Transek 04", length: 1300, avgDepth: 3.6 }],
          },
          {
            surveyId: "SURVEY_1756104960",
            date: "2025-04-08",
            transects: [{ id: "TR_5", name: "Transek 05", length: 900, avgDepth: 3.0 }],
          },
          {
            surveyId: "SURVEY_1756104961",
            date: "2025-04-09",
            transects: [{ id: "TR_6", name: "Transek 06", length: 1400, avgDepth: 3.8 }],
          },
        ];
        setSurveyGroups(fallback);
        setSelectedSurveyIds(["SURVEY_1756104956"]);
      } finally {
        setLoading(false);
      }
    };

    fetchTransects();
  }, []);

  // --- 2. Ambil Titik Sampling per surveyId ---
  useEffect(() => {
    const fetchAllPoints = async () => {
      if (selectedSurveyIds.length === 0) {
        setAllData({});
        return;
      }

      setLoadingPoints(true);
      const newData: Record<string, SamplingPoint[]> = {};

      try {
        for (const surveyId of selectedSurveyIds) {
          const res = await fetch("http://localhost:5000/graphql", {
            method: "POST",
            headers: getAuthHeaders(),
            body: JSON.stringify({
              query: `
                query GetSamplingPoints($surveyId: String!) {
                  samplingPointsBySurveyId(surveyId: $surveyId) {
                    geometry
                    meta
                  }
                }
              `,
              variables: { surveyId },
            }),
          });

          const result = await res.json();
          const points = result.data?.samplingPointsBySurveyId || [];

          const processedPoints = points
            .map((p: any) => {
              const meta = p.meta || {};
              const depth = parseFloat(meta.kedalaman ?? meta.depth_value ?? "0");
              const distance = parseFloat(meta.distance_m ?? "0");

              if (isNaN(depth) || isNaN(distance)) return null;

              return {
                surveyId,
                distance: Math.round(distance * 100) / 100,
                depth: Math.abs(depth),
              };
            })
            .filter(Boolean) as SamplingPoint[];

          processedPoints.sort((a, b) => a.distance - b.distance);
          newData[surveyId] = processedPoints;
        }

        setAllData(newData);
      } catch (err) {
        console.error("‚ùå Gagal muat titik sampling:", err);
        alert(`Gagal memuat data titik.`);
      } finally {
        setLoadingPoints(false);
      }
    };

    fetchAllPoints();
  }, [selectedSurveyIds]);

  // --- 3. Gabungkan semua jarak unik untuk sumbu X ---
  const allDistances = useMemo(() => {
    const distances = new Set<number>();

    // Dari data survey
    Object.values(allData).forEach((points) => points.forEach((p) => distances.add(Math.round(p.distance * 10) / 10)));

    // Dari data echosounder (dari context, bukan localStorage)
    echosounderData.forEach((d) => distances.add(Math.round(d.jarak * 10) / 10));

    return Array.from(distances).sort((a, b) => a - b);
  }, [allData, echosounderData]);

  // --- 4. Siapkan Data Chart ---
  const chartData = useMemo(() => {
    const datasets = [];

    // Tambahkan data dari survey
    datasets.push(
      ...selectedSurveyIds.map((surveyId, idx) => {
        const points = allData[surveyId] || [];
        const colorHue = (idx * 130) % 360;
        const borderColor = `hsl(${colorHue}, 70%, 50%)`;
        const backgroundColor = `hsl(${colorHue}, 70%, 50%, 0.2)`;

        return {
          label: `Survey ${surveyId.slice(-6)}`,
          data: allDistances.map((dist: number) => {
            const point = points.find((p: SamplingPoint) => Math.abs(p.distance - dist) < 0.5);
            return point ? point.depth : null;
          }),
          borderColor,
          backgroundColor,
          fill: true,
          tension: 0.4,
          pointRadius: 4,
          pointHoverRadius: 6,
        };
      })
    );

    // Tambahkan data dari echosounder (dari context)
    if (echosounderData.length > 0) {
      datasets.push({
        label: "Data Lapangan (Echosounder)",
        data: allDistances.map((dist: number) => {
          const point = echosounderData.find((d) => Math.abs(d.jarak - dist) < 0.5);
          return point ? Math.abs(point.kedalaman) : null;
        }),
        borderColor: "hsl(210, 70%, 50%)",
        backgroundColor: "hsl(210, 70%, 50%, 0.2)",
        fill: true,
        tension: 0.4,
        pointRadius: 4,
        pointHoverRadius: 6,
      });
    }

    return {
      labels: allDistances.map((d) => d.toFixed(0)),
      datasets,
    };
  }, [allDistances, allData, selectedSurveyIds, echosounderData]);

  // --- 5. Opsi Chart ---
  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: "top" as const,
        labels: { usePointStyle: true, boxWidth: 6 },
      },
      tooltip: {
        mode: "index" as const,
        intersect: false,
        callbacks: {
          label: (context: any) => `${context.dataset.label}: ${context.parsed.y} m`,
        },
      },
    },
    scales: {
      y: {
        reverse: true,
        title: {
          display: true,
          text: "Kedalaman (m)",
          font: { size: 12 },
        },
        ticks: { stepSize: 1 },
        grid: { color: "rgba(0,0,0,0.05)" },
      },
      x: {
        title: {
          display: true,
          text: "Jarak dari Awal Transek (m)",
          font: { size: 12 },
        },
        grid: { color: "rgba(0,0,0,0.05)" },
      },
    },
  };

  // Data untuk halaman saat ini
  const currentSurveys = surveyGroups.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage);

  // === Render UI ===
  return (
    <div className="p-4 w-full space-y-6 bg-white rounded-lg shadow-sm h-full overflow-y-auto">
      <h2 className="font-bold text-lg text-slate-800">üìä Perbandingan Penampang Transek</h2>

      {/* Select Survey - Grouped Table with Pagination */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">Pilih Survey</label>

        {loading ? (
          <p className="text-sm text-gray-500">Memuat daftar survey...</p>
        ) : surveyGroups.length === 0 ? (
          <p className="text-sm text-gray-400 italic">Tidak ada survey tersedia.</p>
        ) : (
          <>
            <div className="overflow-x-auto border rounded-lg bg-white shadow-sm">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      <input
                        type="checkbox"
                        checked={selectedSurveyIds.length === surveyGroups.length}
                        onChange={(e) => {
                          setSelectedSurveyIds(e.target.checked ? surveyGroups.map((s) => s.surveyId) : []);
                        }}
                      />
                    </th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Survey ID</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Tanggal</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Jumlah Transek</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Panjang Total</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {currentSurveys.map((survey) => {
                    const totalLength = survey.transects.reduce((sum, t) => sum + t.length, 0);
                    return (
                      <tr key={survey.surveyId} className={`transition-colors hover:bg-gray-50 ${selectedSurveyIds.includes(survey.surveyId) ? "bg-blue-50" : ""}`}>
                        <td className="px-4 py-2">
                          <input
                            type="checkbox"
                            checked={selectedSurveyIds.includes(survey.surveyId)}
                            onChange={() => {
                              setSelectedSurveyIds((prev) => (prev.includes(survey.surveyId) ? prev.filter((id) => id !== survey.surveyId) : [...prev, survey.surveyId]));
                            }}
                          />
                        </td>
                        <td className="px-4 py-2 text-sm font-medium">{`SURVEY...${survey.surveyId.slice(-6)}`}</td>
                        <td className="px-4 py-2 text-sm text-gray-500">{survey.date}</td>
                        <td className="px-4 py-2 text-sm text-gray-500">{survey.transects.length}</td>
                        <td className="px-4 py-2 text-sm text-gray-500">{totalLength} m</td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>

            {/* Pagination Controls */}
            <div className="flex items-center justify-between text-sm">
              <p className="text-gray-600">
                Menampilkan {currentSurveys.length} dari {surveyGroups.length} survey
              </p>
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                  disabled={currentPage === 1}
                  className={`px-3 py-1 rounded border ${currentPage === 1 ? "bg-gray-100 text-gray-400 cursor-not-allowed" : "bg-white text-gray-700 hover:bg-gray-50"}`}
                >
                  Previous
                </button>
                <span className="text-gray-700">
                  Halaman {currentPage} dari {totalPages}
                </span>
                <button
                  onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                  disabled={currentPage === totalPages}
                  className={`px-3 py-1 rounded border ${currentPage === totalPages ? "bg-gray-100 text-gray-400 cursor-not-allowed" : "bg-white text-gray-700 hover:bg-gray-50"}`}
                >
                  Next
                </button>
              </div>
            </div>
          </>
        )}
        <p className="text-xs text-gray-500">Pilih survey untuk membandingkan penampang.</p>
      </div>

      {/* Chart */}
      <div className="flex-1 h-80">
        <h3 className="font-medium mb-2 text-slate-700">Grafik Profil Dasar</h3>
        {loadingPoints ? (
          <p className="text-sm text-gray-500">Memuat data titik...</p>
        ) : selectedSurveyIds.length === 0 && echosounderData.length === 0 ? (
          <p className="text-sm text-gray-400 italic">Pilih survey atau upload data lapangan.</p>
        ) : (
          <Line data={chartData} options={chartOptions} />
        )}
      </div>

      {/* Info Statistik */}
      {(selectedSurveyIds.length > 0 || echosounderData.length > 0) && (
        <div className="text-sm text-gray-600 space-y-1 border-t pt-3">
          {selectedSurveyIds.length > 0 && (
            <p>
              <strong>Survey Dipilih:</strong> {selectedSurveyIds.length}
            </p>
          )}
          {echosounderData.length > 0 && (
            <p>
              <strong>Data Lapangan:</strong> {echosounderData.length} titik
            </p>
          )}
          <p>
            <strong>Total Titik:</strong> {Object.values(allData).flat().length + echosounderData.length}
          </p>
          {allDistances.length > 0 && (
            <p>
              <strong>Jarak Total:</strong> {Math.max(...allDistances)} m
            </p>
          )}
        </div>
      )}
    </div>
  );
}
