// src/components/panels/FeaturePanel.tsx
"use client";

import Papa from "papaparse";
import { ChangeEvent, useEffect, useState } from "react";
import { useData } from "@/context/DataContext";
import { useTool } from "@/context/ToolContext";

// --- Tipe Data ---
interface EchosounderPoint {
  jarak: number;
  kedalaman: number;
  offset_y?: number;
  latitude?: number;
  longitude?: number;
}

type DataType = "2d" | "3d" | "gps" | "unknown";

interface FeaturePanelProps {
  activePanel: "rute" | "interpolasi" | "sbn" | "echosounder";
  close: () => void;
}

export default function FeaturePanel({ activePanel, close }: FeaturePanelProps) {
  const { echosounderData, setEchosounderData } = useData(); // Ambil data dan setter
  const { setShowSidebarRight, setShowSurface3D } = useTool();

  // --- State ---
  const [manualJarak, setManualJarak] = useState("");
  const [manualKedalaman, setManualKedalaman] = useState("");
  const [uploadStatus, setUploadStatus] = useState<{ loading: boolean; type: DataType; count: number } | null>(null);

  // --- Deteksi tipe data dari header CSV ---
  const detectDataType = (headers: string[]): DataType => {
    const hasX = headers.some((h) => /jarak|distance/i.test(h));
    const hasZ = headers.some((h) => /kedalaman|depth/i.test(h));
    const hasY = headers.some((h) => /offset|cross|lebar/i.test(h));
    const hasLat = headers.some((h) => /lat/i.test(h));
    const hasLng = headers.some((h) => /lon|lng/i.test(h));

    if (hasX && hasY && hasZ) return "3d";
    if (hasX && hasZ) return "2d";
    if (hasLat && hasLng && hasZ) return "gps";
    return "unknown";
  };

  // --- Parse baris CSV jadi EchosounderPoint[] ---
  const parseCSVRows = (rows: Record<string, string>[]): EchosounderPoint[] => {
    return rows
      .map((row) => ({
        jarak: parseFloat(row.jarak_m || row.distance || "0"),
        kedalaman: parseFloat(row.kedalaman_m || row.depth || "0"),
        offset_y: row.offset_y_m ? parseFloat(row.offset_y_m) : undefined,
        latitude: row.latitude ? parseFloat(row.latitude) : undefined,
        longitude: row.longitude ? parseFloat(row.longitude) : undefined,
      }))
      .filter((d) => !isNaN(d.jarak) && !isNaN(d.kedalaman));
  };

  // --- Input manual ---
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const jarak = parseFloat(manualJarak);
    const kedalaman = parseFloat(manualKedalaman);

    if (isNaN(jarak) || isNaN(kedalaman)) {
      alert("‚ùå Jarak dan kedalaman harus angka.");
      return;
    }

    // Update state global
    setEchosounderData((prev) => [...(Array.isArray(prev) ? prev : []), { jarak, kedalaman }]);

    // Reset input
    setManualJarak("");
    setManualKedalaman("");
  };

  // --- Upload CSV ---
  const handleCSVUpload = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    setUploadStatus({ loading: true, type: "unknown", count: 0 });

    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (results) => {
        const rows = results.data as Record<string, string>[];
        if (!rows.length) {
          setUploadStatus(null);
          alert("‚ùå File CSV kosong.");
          return;
        }

        const headers = Object.keys(rows[0]);
        const dataType = detectDataType(headers);
        if (dataType === "unknown") {
          setUploadStatus(null);
          alert("‚ùå Format CSV tidak dikenali.\nGunakan salah satu:\n- jarak_m, kedalaman_m (2D)\n- jarak_m, offset_y_m, kedalaman_m (3D)\n- latitude, longitude, depth (GPS)");
          return;
        }

        const cleaned = parseCSVRows(rows);
        if (!cleaned.length) {
          setUploadStatus(null);
          alert("‚ùå Tidak ada data valid dalam file.");
          return;
        }

        // Update global state
        setEchosounderData(cleaned);
        setUploadStatus({ loading: false, type: dataType, count: cleaned.length });

        // Auto-buka panel sesuai tipe data
        if (dataType === "3d") {
          setShowSurface3D(true);
        } else {
          setShowSidebarRight(true);
        }
      },
      error: (err) => {
        setUploadStatus(null);
        alert("‚ùå Gagal baca file: " + err.message);
      },
    });
  };

  // --- Sinkronisasi ke localStorage saat echosounderData berubah ---
  useEffect(() => {
    if (Array.isArray(echosounderData)) {
      try {
        localStorage.setItem("echosounderData", JSON.stringify(echosounderData));
      } catch (err) {
        console.warn("Gagal simpan ke localStorage:", err);
      }
    }
  }, [echosounderData]);

  // --- Reset Upload Status saat unmount ---
  useEffect(() => {
    return () => setUploadStatus(null);
  }, []);

  // --- Render UI ---
  return (
    <div className="absolute top-4 left-1/2 -translate-x-1/2 w-[480px] bg-white rounded-xl shadow-2xl p-5 z-50 overflow-auto max-h-[90vh] border border-gray-200">
      {/* Header */}
      <div className="flex justify-between items-center mb-4 pb-3 border-b border-gray-100">
        <h3 className="text-xl font-bold text-slate-800 flex items-center gap-2">
          {activePanel === "echosounder" && "üìä"} {panelTitle[activePanel]}
        </h3>
        <button onClick={close} className="text-red-500 hover:text-red-600 hover:bg-red-50 rounded-full p-1 transition-all" aria-label="Tutup">
          ‚úï
        </button>
      </div>

      {/* Panel Echosounder */}
      {activePanel === "echosounder" && (
        <div className="space-y-5">
          {/* Deskripsi */}
          <p className="text-sm text-gray-600">Masukkan data kedalaman manual atau unggah file CSV.</p>

          {/* Input Manual */}
          <form onSubmit={handleSubmit} className="space-y-3 bg-gray-50 p-4 rounded-lg border">
            <h4 className="font-medium text-slate-700">‚ûï Input Manual</h4>
            <div className="grid grid-cols-2 gap-3">
              <div>
                <label className="text-xs text-slate-600">Jarak (m)</label>
                <input type="number" step="any" value={manualJarak} onChange={(e) => setManualJarak(e.target.value)} required className="w-full border rounded px-3 py-2 text-sm" placeholder="50.5" />
              </div>
              <div>
                <label className="text-xs text-slate-600">Kedalaman (m)</label>
                <input type="number" step="any" value={manualKedalaman} onChange={(e) => setManualKedalaman(e.target.value)} required className="w-full border rounded px-3 py-2 text-sm" placeholder="2.3" />
              </div>
            </div>
            <button type="submit" className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-500 text-sm font-medium">
              Tambah Titik
            </button>
          </form>

          {/* Upload CSV */}
          <div className="bg-blue-50 p-4 rounded-lg border border-blue-200">
            <h4 className="font-medium text-slate-700 mb-1">üìÅ Unggah CSV</h4>
            <input type="file" accept=".csv" onChange={handleCSVUpload} className="text-sm block w-full" />
            {uploadStatus?.loading && <p className="text-xs text-blue-600 mt-2">üîÑ Memproses file...</p>}
            {uploadStatus && !uploadStatus.loading && (
              <p className="mt-2 text-xs text-green-600">
                ‚úÖ {uploadStatus.count} titik dimuat sebagai <b>{uploadStatus.type.toUpperCase()}</b>
              </p>
            )}
          </div>

          {/* Tombol Aksi */}
          <div className="flex gap-2 pt-3 border-t">
            <button
              onClick={() => {
                setShowSidebarRight(true);
                close();
              }}
              className="flex-1 bg-green-600 text-white py-2 rounded hover:bg-green-500 text-sm font-medium"
            >
              üìà Tampilkan 2D
            </button>
            <button
              onClick={() => {
                setShowSurface3D(true);
                close();
              }}
              className="flex-1 bg-purple-600 text-white py-2 rounded hover:bg-purple-500 text-sm font-medium"
            >
              üåê Buka 3D
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

// --- Helper Judul Panel ---
const panelTitle = {
  rute: "Input Data Rute Survey",
  interpolasi: "Interpolasi Data Batimetri",
  sbn: "Sarana Bantu Navigasi",
  echosounder: "Visualisasi Echosounder",
};
